	data() {
		return {
			tabCurrentIndex: 0,
			swiperCurrentIndex: 0,
			titleShowId: 'tabTag-0',
			tabs: [
				//标签名称 , 分类 id , 加载更多, 加载的页码
				{ name: '关注', id: 'pwd1', loadingType: 0, page: 1 },
				{ name: '专题', id: 'pwd2', loadingType: 0, page: 1 },
				{ name: '文集', id: 'pwd3', loadingType: 0, page: 1 }
			],
			showKeyboard: false,
			follows: [],
			userId: uni.getStorageSync('login_key').userId
		};
	},
	onLoad() {},
	onShow: function() {
		this.getFollow();
	},
	methods: {
		getFollow:function(){
			var _this = this;
			uni.request({
				url: this.apiServer + '/follow/list',
				method: 'GET',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					fromUId: uni.getStorageSync('login_key').userId
				},
				success: res => {
					_this.follows = res.data.data;
				}
			});
		},
		cancelFollow: function(toUId) {
			uni.request({
				url: this.apiServer + '/follow/cancel',
				method: 'POST',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					fromUId: this.userId,
					toUId: toUId
				},
				success: res => {
					if (res.data.code === 0) {
						uni.showToast({
							title: '已取消关注'
						});
						this.getFollow();
					}
				}
			});
		},
		goBack:function(){
			uni.navigateBack({
				
			})
		},
		goToUCenter:function(uId){
			uni.navigateTo({
				url: '../usercenter/usercenter?uId=' + uId
			});
		},
		tabChange(e) {
			var index = e.target.id.replace('tabTag-', '');
			this.swiperCurrentIndex = index;
			this.tabCurrentIndex = index;
			this.titleShowId = 'tabTag-' + index;
		},
		swiperChange: function(e) {
			var index = e.detail.current;
			this.tabCurrentIndex = index;
			this.titleShowId = 'tabTag-' + index;
		}
	}
};
